.firstNav{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    font-weight: 700;
    text-shadow: 2px 2px cyan;
    background-color: #050531;
    color: aliceblue;
    padding: 10px 0;
    font-family: cursive;
    transition: all 0.3s;
}
.firstNav:hover{
    letter-spacing: 3px;
}
.firstNav img{
  border-radius: 8px;
    width: 35px;
    margin: 0 10px;
}

.secondNav{
    background-color: #0a0a51;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
}


.radio-input .label input {
    display: none;
  }
  
  .radio-input {
    display: flex;
    gap: 20px;
    font-weight: 700;
    font-size: large;
    color: white;
    text-shadow: 2px 2px #064162;
    font-family: cursive;
  }
  
  .radio-input .label {
    display: flex;
    align-items: center;
  }

  .radio-input .label:hover{
    color: pink;
    text-shadow: 2px 2px #600314;
  }

  .radio-input .label p{
    margin-left: 5px;
  }
  
  .radio-input .label .check {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.3s;
    border: 3px solid transparent;
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.5),
     -1px -1px 4px rgba(255, 255, 255, 0.5);
  }
  
  .radio-input .label input:checked + .check {
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.5),
     -1px -1px 4px rgba(255, 255, 255, 0.5),
     inset 3px 3px 6px rgba(0, 0, 0, 0.5),
     inset -1px -1px 4px rgba(255, 255, 255, 0.5);
    transform: scale(1.1);
  }


  .sign {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* width: 50%; */
    /* height: 50%; */
    background-image: radial-gradient( ellipse 50% 35% at 50% 50%, #6b1839, transparent );
    transform: translate(-50%, -50%);
    letter-spacing: 2;
    left: 80px;
    /* top: 50%; */
    /* font-family: "Clip"; */
    text-transform: uppercase;
    /* font-size: 6em; */
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #ff65bd, -0.2rem 0.1rem 1rem #ff65bd, 0.2rem 0.1rem 1rem #ff65bd, 0 -0.5rem 2rem #ff2483, 0 0.5rem 3rem #ff2483;
    animation: shine 2s forwards, flicker 3s infinite;
  }
  
  @keyframes blink {
    0%,
    22%,
    36%,
    75% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #ff65bd,
        -0.2rem 0.1rem 1rem #ff65bd, 0.2rem 0.1rem 1rem #ff65bd,
        0 -0.5rem 2rem #ff2483, 0 0.5rem 3rem #ff2483;
    }
    28%,
    33% {
      color: #ff65bd;
      text-shadow: none;
    }
    82%,
    97% {
      color: #ff2483;
      text-shadow: none;
    }
  }
  
  .flicker {
    animation: shine 2s forwards, blink 3s 2s infinite;
  }
  
  .fast-flicker {
    animation: shine 2s forwards, blink 10s 1s infinite;
  }
  
  @keyframes shine {
    0% {
      color: #6b1839;
      text-shadow: none;
    }
    100% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #ff65bd,
        -0.2rem 0.1rem 1rem #ff65bd, 0.2rem 0.1rem 1rem #ff65bd,
        0 -0.5rem 2rem #ff2483, 0 0.5rem 3rem #ff2483;
    }
  }
  
  @keyframes flicker {
    from {
      opacity: 1;
    }
  
    4% {
      opacity: 0.9;
    }
  
    6% {
      opacity: 0.85;
    }
  
    8% {
      opacity: 0.95;
    }
  
    10% {
      opacity: 0.9;
    }
  
    11% {
      opacity: 0.922;
    }
  
    12% {
      opacity: 0.9;
    }
  
    14% {
      opacity: 0.95;
    }
  
    16% {
      opacity: 0.98;
    }
  
    17% {
      opacity: 0.9;
    }
  
    19% {
      opacity: 0.93;
    }
  
    20% {
      opacity: 0.99;
    }
  
    24% {
      opacity: 1;
    }
  
    26% {
      opacity: 0.94;
    }
  
    28% {
      opacity: 0.98;
    }
  
    37% {
      opacity: 0.93;
    }
  
    38% {
      opacity: 0.5;
    }
  
    39% {
      opacity: 0.96;
    }
  
    42% {
      opacity: 1;
    }
  
    44% {
      opacity: 0.97;
    }
  
    46% {
      opacity: 0.94;
    }
  
    56% {
      opacity: 0.9;
    }
  
    58% {
      opacity: 0.9;
    }
  
    60% {
      opacity: 0.99;
    }
  
    68% {
      opacity: 1;
    }
  
    70% {
      opacity: 0.9;
    }
  
    72% {
      opacity: 0.95;
    }
  
    93% {
      opacity: 0.93;
    }
  
    95% {
      opacity: 0.95;
    }
  
    97% {
      opacity: 0.93;
    }
  
    to {
      opacity: 1;
    }
  }


  @media only screen and (max-width:500px) {
    .firstNav{
        font-size: 25px;
    }
    .sign{
      margin-top: 15px;
    }
    .secondNav{
      flex-direction: column;
    }
}