/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
  -webkit-appearance: textfield;
  appearance: textfield;
}


#emptyMat{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
}
#emptyMat input{
    color: cyan;
    background-color: rgb(25, 25, 105);
    border: 2px solid cyan;
    padding: 3px;
    text-align: center;
    border-radius: 5px;
    margin: 1px;
    transition: all 0.1s;
    font-size: 16px;
}
#emptyMat input:hover{
    background-color: darkblue;
}
#emptyMat input:focus{
    color: yellow;
    background: linear-gradient(45deg, black, transparent);
    border: 2px solid yellow;
    box-shadow: 2px 2px 3px yellow, -2px -2px 7px cyan;
    outline-color: yellow;
}


.sudoku button {
    margin: 3px;
    --green: #1BFD9C;
    font-size: 15px;
    padding: 0.7em 1.7em;
    letter-spacing: 0.06em;
    position: relative;
    font-family: inherit;
    border-radius: 0.6em;
    overflow: hidden;
    transition: all 0.3s;
    line-height: 1.4em;
    border: 2px solid var(--green);
    background: linear-gradient(to right, rgba(27, 253, 156, 0.1) 1%, transparent 40%,transparent 60% , rgba(27, 253, 156, 0.1) 100%);
    color: var(--green);
    box-shadow: inset 0 0 10px rgba(27, 253, 156, 0.4), 0 0 9px 3px rgba(27, 253, 156, 0.1);
    width: 100px;
  }
  
  .sudoku button:hover {
    color: #82ffc9;
    cursor: pointer;
    box-shadow: inset 0 0 10px rgba(27, 253, 156, 0.6), 0 0 9px 3px rgba(27, 253, 156, 0.2);
  }
  
  .sudoku button:active{
    background-color: rgba(27, 253, 156, 0.6);
  }
  
  .sudoku button:before {
    content: "";
    position: absolute;
    left: -4em;
    width: 4em;
    height: 100%;
    top: 0;
    transition: transform .4s ease-in-out;
    background: linear-gradient(to right, transparent 1%, rgba(27, 253, 156, 0.1) 40%,rgba(27, 253, 156, 0.1) 60% , transparent 100%);
  }
  
  .sudoku button:hover:before {
    transform: translateX(15em);
  }

  .btns{
    display: flex;
    align-items: center;
    justify-content: center;
  }


  #errors, #report{
    display: flex;
    align-items: center;
    justify-content: center;
    color: red;
    font-size: 20px;
    /* margin: 15px 0; */
    text-shadow: 2px 2px black;
    padding: 15px 20px;
  }

  #report{
    color: green;
  }


@media only screen and (max-width:500px) {
    #emptyMat input{
        width: 16px;
    }
}

.fixedNumber{
    background-color: black !important;
    color: yellow !important;
    border-color: yellow !important;
  }